.inputComponentContainer {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  max-width: 211px;
  min-height: 34px;
  margin-bottom: 10px;
}

.fakeLabel {
  font-weight: 400;
  font-size: 8px;
  margin-bottom: 3px;
}

.inputContainer {
  display: flex;
  border-radius: 5px;
  border: 1px solid #d8d8d8;
  box-shadow: none;
  width: 100%;
  box-sizing: border-box;

  &.red {
    border: 1px solid #d91313;
  }

  input {
    background: white;
    border: none;
    margin: 5px 10px;
    font-size: 10px;
    width: 100%;
  }
}

.textAreaComponentContainer {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  max-width: 420px;
  min-height: 67px;
  margin-bottom: 10px;

  .textareaContainer {
    width: 100%;
    height: 55px;
    display: flex;
    border-radius: 5px;
    border: 1px solid #d8d8d8;
    box-shadow: none;
    min-width: 420px;
    box-sizing: border-box;

    .textarea {
      background: white;
      resize: none;
      border: none;
      margin: 5px 10px;
      font-size: 10px;
      width: 100%;
      font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
        "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif;
    }
  }
}
